.contactbar-container {
  height: 28px;
  width: 100%;
  top: 0px;
  position: fixed;
  z-index: 20;
}

.contactbar-active {
  transition: all 1s;
  position: absolute;
  height: 328px;
  width: 100%;
  background-color: var(--fonce);
  color: var(--clair);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 60;
  font-size: 20px;
}
.contactbar-active .contactbar-content {
  text-align: center;
}
.contactbar-active .contactbar-content a {
  color: var(--clair);
  line-height: 20px;
}
.contactbar-active .contactbar-content a svg {
  font-size: 25px;
}
.contactbar-active .contactbar-item {
  display: block;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 20px 0;
}
.contactbar-active .contactbar-chevron {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 5px;
  font-size: 14px;
}
.contactbar-active .contactbar-chevron h3 {
  margin: 0 20px;
}
.contactbar-active .contactbar-chevron .contactbar-chevron-img1 {
  transform: rotate(540deg);
  transition: all 1s;
}
.contactbar-active .contactbar-chevron .contactbar-chevron-img2 {
  transform: rotate(-540deg);
  transition: all 1s;
}
.contactbar-active .contactbar-icone-insta {
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 3px solid var(--clair);
  border-radius: 99999px;
  background-color: var(--rose);
  transition: all 0.5s;
}
.contactbar-active .contactbar-icone-insta svg {
  transform: scale(1.5);
}
.contactbar-active .contactbar-icone-insta:hover {
  border: 3px solid var(--rose);
  background-color: var(--clair);
  box-shadow: 0px 0px 10px 5px var(--rose);
}
.contactbar-active .contactbar-icone-insta:hover svg {
  color: var(--rose);
}

.contactbar-cache {
  position: fixed;
  top: 0px;
  transition: all 1s;
  position: absolute;
  height: 28px;
  width: 100%;
  background-color: var(--fonce);
  color: var(--clair);
  display: flex;
  justify-content: center;
  align-items: center;
}
.contactbar-cache .contactbar-content {
  display: none;
}
.contactbar-cache .contactbar-chevron {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 5px;
  font-size: 14px;
}
.contactbar-cache .contactbar-chevron h3 {
  margin: 0 20px;
}
.contactbar-cache .contactbar-chevron .contactbar-chevron-img1 {
  transform: rotate(0deg);
  transition: all 1s;
}
.contactbar-cache .contactbar-chevron .contactbar-chevron-img2 {
  transform: rotate(0deg);
  transition: all 1s;
}

.contactbar-overlay {
  z-index: 15;
  position: absolute;
  top: -200vh;
  left: 0;
  width: 100%;
  min-height: 2500vh;
  background-color: rgba(51, 51, 51, 0.3137254902);
}

.contactbar-overlay-cache {
  display: none;
}

@media screen and (min-width: 768px) {
  .contactbar-container {
    position: fixed;
    top: calc(100vh - 335px);
    height: 35px;
    transition: all 1s;
    z-index: 60;
  }
  .contactbar-cache {
    position: absolute;
    top: 300px;
    z-index: 60;
    height: 35px;
    transition: all 1s;
  }
  .contactbar-cache .contactbar-chevron {
    transform: rotate(180deg);
    transition: all 1s;
  }
  .contactbar-cache .contactbar-chevron h3 {
    transform: rotate(180deg);
    font-size: 22px;
  }
  .contactbar-cache .contactbar-chevron .contactbar-chevron-img1 {
    transform: rotate(0deg);
    font-size: 22px;
  }
  .contactbar-cache .contactbar-chevron .contactbar-chevron-img2 {
    transform: rotate(0deg);
    font-size: 22px;
  }
  .contactbar-active {
    top: 100%;
    font-size: 20px;
    transition: all 1s;
    z-index: 60;
  }
  .contactbar-active .contactbar-chevron {
    bottom: calc(100% - 28px);
    transform: rotate(180deg);
    font-size: 22px;
  }
  .contactbar-active .contactbar-chevron h3 {
    transform: rotate(180deg);
    font-size: 22px;
  }
}
