.navigation-container {
  width: 100%;
  height: 135px;
  background-color: var(--clair);
  padding-top: 20px;
  position: fixed;
  bottom: -35px;
  z-index: 10;
}

.navigation {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 95%;
  height: 70px;
  margin: 0 auto;
  border-radius: 10px;
  background-color: var(--fonce);
  position: relative;
}
.navigation ul {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}
.navigation ul li {
  position: relative;
  width: 70px;
  height: 70px;
  z-index: 1;
}
.navigation ul li a {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column-reverse;
  width: 100%;
  text-align: center;
  font-weight: bold;
  color: var(--clair);
}

.nav-icon {
  position: relative;
  display: block;
  line-height: 75px;
  font-size: 25px;
  text-align: center;
  z-index: 5;
}

a svg {
  transition: 0.5s;
}

a.active svg {
  transform: translateY(-39px);
  color: var(--fonce);
  font-size: 25px;
}

a .nav-text {
  position: absolute;
  transition: 0.5s;
  font-size: 14px;
  opacity: 0;
  transform: translateY(20px);
}

a.active .nav-text {
  opacity: 1;
  transform: translateY(15px);
  color: var(--clair);
}

.indicator {
  position: absolute;
  top: -53%;
  border-radius: 999px;
  width: 70px;
  height: 70px;
  background: var(--marron);
  border: 8px solid var(--clair);
  opacity: 0;
  transform: scale(0.1);
  transition: 0.5s;
}
.indicator::before {
  content: "";
  position: absolute;
  top: 56%;
  left: -22px;
  width: 20px;
  height: 20px;
  background: transparent;
  border-top-right-radius: 40px;
  box-shadow: 1px -10px 0 0 var(--clair);
}
.indicator::after {
  content: "";
  position: absolute;
  top: 56%;
  right: -22px;
  width: 20px;
  height: 20px;
  background: transparent;
  border-top-left-radius: 22px;
  box-shadow: -1px -10px 0 0 var(--clair);
}

a.active .indicator {
  display: block;
  transform: scale(1);
  opacity: 1;
}

.navigation-overlay {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: var(--clair);
}

@media screen and (min-width: 768px) {
  .navigation-container {
    top: -10px;
  }
  .navigation {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
    max-width: 1000px;
    height: 100px;
  }
  .nav-icon {
    font-size: 40px;
  }
  a.active svg {
    transform: translateY(52px);
    font-size: 35px;
  }
  a .nav-text {
    font-size: 20px;
    transform: translateY(-40px);
  }
  a.active .nav-text {
    transform: translateY(-22px);
  }
  .indicator {
    width: 80px;
    height: 80px;
    top: 60%;
    transition: 0.5s;
    opacity: 0;
  }
  .indicator::before {
    content: "";
    top: 15px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 20px;
    box-shadow: 0 10px 0 0 var(--clair);
  }
  .indicator::after {
    content: "";
    top: 15px;
    border-top-left-radius: 0;
    border-bottom-left-radius: 20px;
    box-shadow: 0 10px 0 0 var(--clair);
  }
}
